import './App.css';
import TodoList from './TodoList/TodoList';
import { useState, useEffect } from 'react';
import SortDate from './mainInterface/SortDate';
import Title from './Title/Title';
import FilterStatus from './mainInterface/filterStatus';
import Input from './input/Input';
import Pagination from './pagination/Pagination';
import axios from 'axios';

const App = () => {
  const [todos] = useState([]);
  const [text, setText] = useState("");
  const [filter, setFilter] = useState("undone");
  const [filtered, setFiltered] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [flag, setFlag] = useState(false);
  const todosPerPage = 5;
  useEffect(() => {
    some();
  }, [todos, filter, flag])

  const some = async () => {
    try {
      const href = `https://todo-api-learning.herokuapp.com/v1/tasks/3?${filter ? `filterBy=${filter}` : ''}&order=asc`
      const result = await axios.get(href);
      setFiltered(result.data)
    } catch (err) {
      console.log(err.response.data.message);
      alert(err.response.data.message);
    }
  }

  const handleCreateTodos = async (e, name, todo) => {
    try {
      e.preventDefault();
      await axios.post("https://todo-api-learning.herokuapp.com/v1/task/3", { name: name, done: false })
      setFlag(!flag)
      setText("");
    } catch (err) {
      console.log(err.response.data.message);
      alert(err.response.data.message);
    }
  }



  const changeStatus = async (todo) => {
    try {
      await axios.patch(`https://todo-api-learning.herokuapp.com/v1/task/3/${todo.uuid}`, { name: todo.name, done: !todo.done });
      some();
    } catch (err) {
      console.log(err.response.data.message);
      alert(err.response.data.message);
    }
  }

  const IndexOfLastTodo = currentPage * todosPerPage;
  const indexOfFirstTodo = IndexOfLastTodo - todosPerPage;

  const removeItem = async (id) => {
    try {
      await axios.delete(`https://todo-api-learning.herokuapp.com/v1/task/3/${id}`);
      if (filtered.slice(indexOfFirstTodo, IndexOfLastTodo).length === 1) {
        setCurrentPage(currentPage - 1);
      }
      setFlag(!flag);
    } catch (err) {
      console.log(err.response.data.message);
      alert(err.response.data.message);
    }
  };

  const paginate = (pageNumber) => setCurrentPage(pageNumber);
  return (
    <div className="container">
      <Title />
      <div className="mainInput">
        <Input
          text={text}
          setText={setText}
          handleCreateTodos={handleCreateTodos} />
      </div>
      <div className="mainInterface">
        <FilterStatus
          setFilter={setFilter}
        />
        <SortDate
          filtered={filtered}
          setFiltered={setFiltered} />
      </div>
      <div className="todoList">
        <TodoList
          filtered={filtered}
          IndexOfLastTodo={IndexOfLastTodo}
          indexOfFirstTodo={indexOfFirstTodo}
          removeItem={removeItem}
          changeStatus={changeStatus}
        />
      </div>
      <Pagination
        todosPerPage={todosPerPage}
        totalTodos={filtered.length}
        paginate={paginate} />
    </div>
  );
}

export default App;
